<template lang="pug">
include ../../../mixins/bemto/bemto
+b.input
  +e.label.LABEL.heading-text.--h3.--bold(:for="fieldName") {{ properties.label }}
  +e.item
    component(
      :id="properties.fieldName",
      :is="properties.childComponentName",
      :propsToMerge="propsToMerge",
      @switch-handler="transportSwitchHandler",
      @input-handler="transportInputHandler"
    )
  +e.message(:class="messageModifier") {{ properties.message }}
</template>

<script>
import propertiesHandler from '@/mixins/propertiesHandler'
export default {
  mixins: [propertiesHandler],
  name: 'Input',
  data: function() {
    return {}
  },
  props: {
    elements: {
      type: Array,
      default() {
        return []
      },
    },
    fieldName: {
      type: String,
      default() {
        return ''
      },
    },
    label: {
      type: String,
      default() {
        return ''
      },
    },
    message: {
      type: String,
      default() {
        return ''
      },
    },
    messageType: {
      type: String,
      default() {
        return ''
      },
    },
  },
  computed: {
    messageModifier() {
      return this.properties.messageType
        ? `input__message--${this.properties.messageType}`
        : ''
    },
  },
  methods: {
    transportSwitchHandler() {
      this.$emit('switch-handler')
    },
    transportInputHandler(value, model) {
      this.$emit('input-handler', value, model)
    },
  },
}
</script>
